import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '@/views/Main'
import Login from '@/views/Login'
import UserCenter from '@/views/UserCenter'
import PortalCenter from '@/views/PortalCenter'
import JinPiao from '@/views/JinPiao'
import Commission from '@/views/Commission'
import Workbench from '@/views/Workbench'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: 'login'
  },
  {
    path: '/',
    name: 'Main',
    component: Main,
    children: [
      {
        path: 'user',
        component: UserCenter
      },
      {
        path: 'portal',
        component: PortalCenter
      },
      {
        path: 'jinpiao',
        component: JinPiao
      },
      {
        path: 'commission',
        component: Commission
      },
      {
        path: "workbench",
        component: Workbench
      }
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
  },
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
